export function isvalidUsername(str: string) {
	const valid_map = ['admin', 'editor'];
	return valid_map.indexOf(str.trim()) >= 0;
}

/* uri*/
export function validateURL(textval: string) {
	const urlregex =
		/^(https?|ftp):\/\/([a-zA-Z0-9.-]+(:[a-zA-Z0-9.&%$-]+)*@)*((25[0-5]|2[0-4][0-9]|1[0-9]{2}|[1-9][0-9]?)(\.(25[0-5]|2[0-4][0-9]|1[0-9]{2}|[1-9]?[0-9])){3}|([a-zA-Z0-9-]+\.)*[a-zA-Z0-9-]+\.(com|edu|gov|int|mil|net|org|biz|arpa|info|name|pro|aero|coop|museum|[a-zA-Z]{2}))(:[0-9]+)*(\/($|[a-zA-Z0-9.,?'\\+&%$#=~_-]+))*$/;
	return urlregex.test(textval);
}
/**
 * 
 * @param {*} s
 */
export function isEmail(s: string) {
	return /^([a-zA-Z0-9_-])+@([a-zA-Z0-9_-])+((.[a-zA-Z0-9_-]{2,3}){1,2})$/.test(s);
}

/**
 * 
 * @param {*} s
 */
export function isMobile(s: string) {
	return /^1[0-9]{10}$/.test(s);
}

/**
 * 
 * @param {*} s
 */
export function isPhone(s: string) {
	return /^([0-9]{3,4}-)?[0-9]{7,8}$/.test(s);
}

/**
 * URL
 * @param {*} s
 */
export function isURL(s: string) {
	return /^http[s]?:\/\/.*/.test(s);
}

/* */
export function validateLowerCase(str: string) {
	const reg = /^[a-z]+$/;
	return reg.test(str);
}

/* */
export function validateUpperCase(str: string) {
	const reg = /^[A-Z]+$/;
	return reg.test(str);
}

/* */
export function validatAlphabets(str: string) {
	const reg = /^[A-Za-z]+$/;
	return reg.test(str);
}
/*padpc*/
export const vaildatePc = function () {
	const userAgentInfo = navigator.userAgent;
	const Agents = ['Android', 'iPhone', 'SymbianOS', 'Windows Phone', 'iPad', 'iPod'];
	let flag = true;
	for (let v = 0; v < Agents.length; v++) {
		if (userAgentInfo.indexOf(Agents[v]) > 0) {
			flag = false;
			break;
		}
	}
	return flag;
};
/**
 * validate email
 * @param email
 * @returns {boolean}
 */
export function validateEmail(email: string) {
	const re =
		/^(([^<>()\\[\]\\.,;:\s@"]+(\.[^<>()\\[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;
	return re.test(email);
}

/**
 * 
 */
export function cardid(code: string) {
	let list = [];
	let result = true;
	let msg = '';
	let city = {
		11: '',
		12: '',
		13: '',
		14: '',
		15: '',
		21: '',
		22: '',
		23: ' ',
		31: '',
		32: '',
		33: '',
		34: '',
		35: '',
		36: '',
		37: '',
		41: '',
		42: ' ',
		43: '',
		44: '',
		45: '',
		46: '',
		50: '',
		51: '',
		52: '',
		53: '',
		54: ' ',
		61: '',
		62: '',
		63: '',
		64: '',
		65: '',
		71: '',
		81: '',
		82: '',
		91: ' '
	};
	if (!validatenull(code)) {
		if (code.length == 18) {
			if (!code || !/(^\d{18}$)|(^\d{17}(\d|X|x)$)/.test(code)) {
				msg = '';
			} else if (!city[code.substr(0, 2)]) {
				msg = '';
			} else {
				//18
				let codeArr = code.split('');
				//(aiWi)(mod 11)
				//
				let factor = [7, 9, 10, 5, 8, 4, 2, 1, 6, 3, 7, 9, 10, 5, 8, 4, 2];
				//
				let parity = [1, 0, 'X', 9, 8, 7, 6, 5, 4, 3, 2, 'x'];
				let sum = 0;
				let ai = 0;
				let wi = 0;
				for (let i = 0; i < 17; i++) {
					ai = parseInt(codeArr[i]);
					wi = factor[i];
					sum += ai * wi;
				}
				if (parity[sum % 11] != code[17]) {
					msg = '';
				} else {
					result = false;
				}
			}
		} else {
			msg = '18';
		}
	} else {
		msg = '';
	}
	list.push(result);
	list.push(msg);
	return list;
}
/**
 * 
 */
export function isvalidatemobile(phone: string) {
	let list = [];
	let result = true;
	let msg = '';
	let isPhone = /^0\d{2,3}-?\d{7,8}$/;
	//134 |1349[0-9]{7}181,14517[678]
	if (!validatenull(phone)) {
		if (phone.length == 11) {
			if (isPhone.test(phone)) {
				msg = '';
			} else {
				result = false;
			}
		} else {
			msg = '11';
		}
	} else {
		msg = '';
	}
	list.push(result);
	list.push(msg);
	return list;
}
/**
 * 
 */
export function validatename(name: string) {
	let regName = /^[\u4e00-\u9fa5]{2,4}$/;
	if (!regName.test(name)) return false;
	return true;
}
/**
 * 
 */
export function validatenum(num: string, type: number) {
	let regName = /[^\d.]/g;
	if (type == 1) {
		if (!regName.test(num)) return false;
	} else if (type == 2) {
		regName = /[^\d]/g;
		if (!regName.test(num)) return false;
	}
	return true;
}
/**
 * 
 */
export function validatenumord(num: string, type: number) {
	let regName = /[^\d.]/g;
	if (type == 1) {
		if (!regName.test(num)) return false;
	} else if (type == 2) {
		regName = /[^\d.]/g;
		if (!regName.test(num)) return false;
	}
	return true;
}
/**
 * 
 */
export function validatenull(val: string | [] | {} | null | undefined) {
	if (typeof val == 'boolean') {
		return false;
	}
	if (typeof val == 'number') {
		return false;
	}
	if (val instanceof Array) {
		if (val.length == 0) return true;
	} else if (val instanceof Object) {
		if (JSON.stringify(val) === '{}') return true;
	} else {
		if (val == 'null' || val == null || val == 'undefined' || val == undefined || val == '') return true;
		return false;
	}
	return false;
}
